#menu-target
  z-index: 1
  background-color: #333337
  overflow: hidden
  width: 250px
  height: 100vh
  position: fixed
  left: 0
  top: 0

  ul
    padding: 10px 0

  li
    margin: 0
    padding: 10px 0
    list-style: none

    i
      padding: 0 20px
      color: #DADADA
      font-style: normal
      +font-size(14)

    a
      color: #DADADA
      font-family: $sans
      font-weight: 700
      border-bottom: 0 transparent
      +font-size(12)

      &:hover
        border-bottom: none
        color: #fff

  hr
    margin: 20px auto
    width: 40%

.menu-trigger
  top: 15px
  left: 15px
  z-index: 1080
  position: absolute
  display: block
  height: 40px
  width: 40px
  background: #333
  padding-top: 8px
  cursor: pointer

  span
    height: 5px
    width: 28px
    float: left
    display: block
    margin: 0 6px 5px
    background: #fff
